import React, {Component} from 'react';
import LeftFixed from "./LeftFixed";
import MainNotFixed from "./MainNotFixed";

import './main-content.css'
import Nav from "./Header/Nav";
import Message1 from "../Message/Message1";
import RightFixed from "./RightFixed";

class MainHome extends Component {

    constructor(props) {
        super(props);
        this.state = {

        }
    }

    getProfileImage = (path) => {
        console.log(path)
        this.setState({
            profileImage: path
        })
    };
    render() {
        return (
            <div>
                <Nav/>
                <div className="content">
                    <div className="wrapper">
                        <LeftFixed
                            getProfileImage={(path) => this.getProfileImage(path)}
                        />
                        <MainNotFixed
                        profileImage={this.state.profileImage}
                        />
                        <RightFixed/>
                        <Message1/>
                    </div>
                </div>
            </div>
        );
    }
}

export default MainHome;
