export const like = (postID, like) => async dispatch => {
    try {

        await axios.put(API_URL_POST + "/like/" + postID, {like}, {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        });
        dispatch({
            type: types.GET_CURRENT_USER_POSTS
        });

    } catch (err) {
    }
};

