import React, {Component} from 'react';
import * as postActions from "../../Actions/post.action";
import {connect} from "react-redux";
import upload from "../images/profile/upload.png"

const user = JSON.parse(localStorage.getItem('user'));

class MainNotFixed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            file: "",
            quotes: "",
            userPosts: [],
            like: 0,
            dislike: 0,
            profileImage: null
        }
    }

    componentWillReceiveProps(nextProps, nextContext) {
        this.setState({
            userPosts: nextProps.userPosts,
            profileImage: nextProps.profileImage
        })
    }

    componentDidMount() {
        this.props.getCurrentUserPosts()
    }

    isChangeUpLoad = (e) => {

        this.setState({
            file: e.target.files[0],
        })
    };
    isChange = (e) => {
        this.setState({
            quotes: e.target.value
        })
    };
    handleSubmit = async (e) => {
        e.preventDefault();
        const formData = new FormData();
        formData.append('file', this.state.file);
        formData.append('upload_preset', 'tuanpham');
        const post = await postActions.addPost(formData, this.state.quotes);
        let arr = this.state.userPosts;
        arr.unshift({...post, firstName: user.firstName, lastName: user.lastName});
        this.setState({
            userPosts: arr,
            quotes: "",
            file: ""
        });
    };
    onClickLike = (postID, like) => {
        this.props.like(postID, like);
        this.setState({
            like: like
        })

    };
    onClickDislike = (postID, dislike) => {
        this.props.dislike(postID, dislike);
        this.setState({
            dislike: dislike
        })
    };

    render() {
        let profileImage = this.props.profileImage;

        return (
            <div className="mainnotfixed" id="mainnotfixed">
                <div
                    className="main mainpost"
                    style={{marginBottom: 20, paddingBottom: 10}}
                >
                    <form onSubmit={this.handleSubmit}>
                        <div className="userimg">
                            {profileImage ? <img src={profileImage} alt="profile"/> :user.profileImage?
                                <img src={user.profileImage} alt="profile"/>: <img src={upload} alt="profile"/>}
                        </div>
                        <p className="quotes">
                          <textarea
                              id="mypara"
                              className="txtarea"
                              placeholder="Tun i, bn ang ngh g?"
                              onChange={this.isChange}
                              value={this.state.quotes}
                              required
                          />
                        </p>
                        {/* image load to post */}
                        <div className="post">
                            <img id="load2" className="postimg" src=" "/>
                        </div>
                        <div className="postbar">
                            <input
                                type="file"
                                id="chooseimg"
                                name="file"
                                onChange={this.isChangeUpLoad}
                                onClick={e => (e.target.value = null)}
                            />
                            <button type="button" className="imgbttn" id="imgbttn">
                                 Images
                            </button>
                            <button
                                type="submit"
                                id="postmypost"
                                className="postmypost button gradient-button text-center"
                            >
                                Post
                            </button>
                        </div>
                    </form>
                </div>
                <hr/>
                <div className="allpost">
                    {
                        this.state.userPosts.map((value, index) => (
                            <div className="mainpost" key={index}>
                                <div className="userimg">
                                    {value.userId.toString() === user._id.toString() && profileImage ?
                                        <img src={profileImage} alt="anh dai dien"/> : value.profileImage ?
                                            <img src={value.profileImage} alt="anh dai dien"/> :
                                            <img src={upload} alt="anh dai dien"/>}

                                </div>
                                <div className="username">
                                    {" "}
                                    <p className="name">{value.lastName} {value.firstName}</p>
                                </div>
                                <p className="time">8min ago</p>
                                <p className="quotes">
                                    {value.quotes}
                                </p>
                                <div className="post">
                                    <img className="postimg" src={value.postImg}/>
                                </div>
                                <div className="likedislike">
                                    <p className="like">
                                        <span className="nooflike"
                                              id="like2">{value.like}{" "}</span>{" "}likes &nbsp;{" "}
                                        <span className="noofdislike" id="dislike2">{value.dislike}{" "}
                              </span>{" "}dislikes</p><p className="likedisbttn">
                              <span
                                  id="thumbsup2"
                                  className="fa fa-thumbs-up"
                                  onClick={() => this.onClickLike(value._id, ++value.like)}
                              />{" "}
                                    <span
                                        id="thumbsdown2"
                                        className="fa fa-thumbs-down"
                                        onClick={() => this.onClickDislike(value._id, ++value.dislike)}
                                    />
                                </p>
                                </div>
                            </div>
                        ))
                    }

                </div>
                <button type="button" id="viewmore" className="viewmore">
                    View More
                </button>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    userPosts: state.postReducer
});
const mapDispatchToProps = dispatch => ({
    getCurrentUserPosts: () => dispatch(postActions.getCurrentUserPosts()),
    like: (postID, like) => dispatch(postActions.like(postID, like)),
    dislike: (postID, dislike) => dispatch(postActions.dislike(postID, dislike))

});
export default connect(mapStateToProps, mapDispatchToProps)(MainNotFixed);