import * as types from '../Constants/index.constant.js'
import axios from "axios";

const token = JSON.parse(localStorage.getItem("jwt"));


const API_URL_CHAT = "/api/chat";

export const getMessByRoomId = async (roomId) => {
    try {

        const res = await axios.get(API_URL_CHAT +"/getMessByRoomId/"+ roomId, {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        });

        const {data} = res;
        console.log(data);
        return data.messages

    } catch (err) {

    }
};


export const chatRoom = (roomId) => async dispatch => {
    try {

        const res = await axios.post(API_URL_CHAT + "/chatRoom/" + roomId, {}, {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        });

        const {data} = res;
        console.log(data)
        dispatch({
            type: types.NEW_CHAT_ROOM
        });

    } catch (err) {
    }
};

