import React, {Component} from 'react';
import {BrowserRouter, Switch} from 'react-router-dom'
import MainAuth from "./Components/Auth/MainAuth";
import PrivateRoute from './Components/Auth/PrivateRoute'
import PublicRoute from './Components/Auth/PublicRoute'
import MainHome from "./Components/Home/MainHome";
import MainProfile from "./Components/Profile/MainProfile";
import Chat from "./Components/Message/chat";
import MainMessage from "./Components/Message/MainMessage";


class App extends Component {
    render() {
        return (
            <BrowserRouter>
                <Switch>
                    <PublicRoute component={MainMessage} path="/home/message/:userId" exact/>
                    <PrivateRoute component={MainProfile} path="/home/profile/:userId" exact/>
                    <PublicRoute restricted={true} component={MainAuth} path="/" exact/>
                    <PublicRoute restricted={false} component={Chat} path="/chat" exact/>
                    <PrivateRoute component={MainHome} path="/home" exact/>
                </Switch>
            </BrowserRouter>
        )
    }
}


export default App;
