import React, {Component} from 'react';
import {connect} from 'react-redux'
import * as actions from "../../Actions/auth.action";

class RegisterForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: "",
            password: "",
            firstName: "",
            lastName: "",
            day: "31",
            month: "7",
            year: "1998",
            male: true,
            female: false,
            other: "Tu chnh",
            sex: Boolean,
            message: "",
            successful: false,
        };
    }

    isChange = (event) => {
        this.setState({
            [event.target.name]: event.target.value
        });
    };

    handleRegister = (e) => {
        e.preventDefault();
        const birthday = this.state.year + "-" + this.state.month + "-" + this.state.day;
        if (!checkPassword(this.state.password)) return this.setState({
            message: "Mt khu khng nn rng, 8 k t ti thiu, t nht mt ch ci, mt s v mt k t c bit",
            successful: false,
            password: "",
        });
        this.props.register(this.state.email, this.state.password, this.state.firstName, this.state.lastName, birthday, this.state.sex)
         this.setState({
            email: "",
            password: "",
        });

    };

    render() {
        console.log(this.props.message)
        return (
            <div className="form-container sign-up-container">
                <form onSubmit={this.handleRegister}>
                    <h1>ng K</h1>
                    <div className="social-container">
                        <a href="#1" className="social">
                            <i className="fa fa-facebook-f"/>
                        </a>
                        <a href="#1" className="social">
                            <i className="fa fa-google"/>
                        </a>
                        <a href="#1" className="social">
                            <i className="fa fa-twitter"/>
                        </a>
                    </div>
                    <span>hoc s dng email ca bn  ng k</span>
                    {(this.props.message || this.state.message) && (
                        <span className={this.props.successful ? "alert alert-success" : "alert alert-danger"}
                              role="alert">{this.props.message || this.state.message}</span>
                    )}
                    <div className="form-row">
                        <div className="col">
                            <input type="text"
                                   name="lastName"
                                   className="txt input-row"
                                   placeholder="H lt"
                                   required
                                   value={this.state.lastName}
                                   onChange={this.isChange}
                            />
                        </div>
                        <div className="col">
                            <input type="text"
                                   name="firstName"
                                   className="txt input-row"
                                   placeholder="Tn"
                                   required
                                   value={this.state.firstName}
                                   onChange={this.isChange}
                            />
                        </div>
                    </div>
                    <input type="email"
                           name="email"
                           className="txt"
                           placeholder="Email"
                           required
                           value={this.state.email}
                           onChange={this.isChange}
                    />
                    <input type="password"
                           name="password"
                           className="txt"
                           placeholder="Mt khu mi"
                           required
                           value={this.state.password}
                           onChange={this.isChange}
                    />

                    <div className="form-group">
                        <label className="mb-label-day">Ngy sinh
                        </label>
                        <div className="form-row">
                            <div className="col">
                                <select className="custom-select"
                                        name="day"
                                        value={this.state.day}
                                        onChange={this.isChange}
                                >
                                    <option value={1}>1</option>
                                    {
                                        [...Array(30)].map(((value, i) => (
                                                <option key={i} value={i + 2}>{i + 2}</option>
                                            )
                                        ))
                                    }
                                </select>
                            </div>
                            <div className="col">
                                <select className="custom-select"
                                        id="custom-select-month"
                                        name="month"
                                        value={this.state.month}
                                        onChange={this.isChange}
                                >
                                    <option value={1}>Thng 1</option>
                                    {
                                        [...Array(11)].map(((value, i) => (
                                                <option key={i} value={i + 2}>Thng {i + 2}</option>
                                            )
                                        ))
                                    }
                                </select>
                            </div>
                            <div className="col">
                                <select className="custom-select"
                                        name="year"
                                        value={this.state.year}
                                        onChange={this.isChange}
                                >
                                    <option>1930</option>
                                    {
                                        [...Array(90)].map(((value, i) => (
                                                <option key={i} value={i + 1931}>{i + 1931}</option>
                                            )
                                        ))
                                    }
                                </select>
                            </div>
                        </div>
                    </div>
                    <fieldset className="form-group">
                        <legend className="col-form-label mb-label-sex">Gii tnh</legend>
                        <div className="form-row mar-left">
                            <div className="form-check form-check-inline col">
                                <input
                                    className="form-check-input"
                                    type="radio"
                                    id="gridRadios1"
                                    name="sex"
                                    onChange={this.isChange}
                                    defaultValue={this.state.male}
                                    required
                                />
                                <label className="form-check-label" htmlFor="gridRadios1">
                                    Nam
                                </label>
                            </div>
                            <div className="form-check form-check-inline col">
                                <input
                                    className="form-check-input"
                                    type="radio"
                                    name="sex"
                                    onChange={this.isChange}
                                    id="gridRadios2"
                                    defaultValue={this.state.female}
                                    required
                                />
                                <label className="form-check-label" htmlFor="gridRadios2">
                                    N
                                </label>
                            </div>
                        </div>
                    </fieldset>
                    <button id="submitbtn">ng K</button>
                </form>
            </div>
        );
    }
}

const checkPassword = (password) => {
    const regex = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9]).{8,}$/;
    return regex.exec(password) !== null;
};

const mapStateToProps = state => ({
    message: state.authReducer.message,
    successful: state.authReducer.successful
});
const mapDispatchToProps = dispatch => ({
    register: (email, password, firstName, lastName, birthday, sex) => dispatch(actions.register(email, password, firstName, lastName, birthday, sex))

});
export default connect(mapStateToProps, mapDispatchToProps)(RegisterForm);